<template>
  <div
    ref="lifeCycleEl"
    class="life-cycle-wrap j-flex j-justify-center j-relative"
  >
    <div
      ref="blockEl"
      class="life-cycle screen-main shadow j-overflow-hidden j-relative j-bg-white"
    >
      <!--高斯模糊位置 -->
      <div ref="blurEl" class="filter-blur j-absolute"></div>
      <div class="j-relative j-z-20">
        <screen-title :info="titleInfo" class="title" />
        <table class="life-cycle-table">
          <tbody>
            <tr v-for="(it, idx) of lifeCycleData" :key="idx">
              <td
                v-for="(itChild, idxChild) of it"
                :key="idxChild"
                :class="[itChild.type, itChild.className]"
              >
                <div
                  v-if="itChild.type === 'col'"
                  class="j-flex j-items-center j-justify-center"
                >
                  {{ itChild.content }}
                </div>
                <div
                  v-else-if="itChild.type === 'row'"
                  class="j-flex j-items-center j-justify-center"
                >
                  <span>{{ itChild.content }}</span>
                  <img :src="ToRightPng" class="j-ml-1" />
                </div>
                <ul v-else class="j-flex j-flex-wrap j-text-sm">
                  <li
                    v-for="(text, idxText) of itChild.content"
                    :key="idxText"
                    :style="{ width: `${100 / itChild.columns}%` }"
                    class="j-text-center j-mb-2"
                  >
                    {{ text }}
                  </li>
                </ul>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <img ref="icon1El" :src="icons.lifeCycle1" class="life-cycle1 j-absolute" />
    <img ref="icon2El" :src="icons.lifeCycle2" class="life-cycle2 j-absolute" />
    <img ref="icon3El" :src="icons.ring2" class="life-cycle3 j-absolute" />
  </div>
</template>
<script>
/**
 * 随屏动画
 * 组件挂载后 注册滚动条事件
 * 组件销毁，销毁滚动条监听  onUnmounted
 *  随着滚动条的移动，一些元素做出一些位置和高度的变化。
 *    开始条件：当滚动条进入一定的区间内，触发
 *      变化区间高度 height = 500px
 *      开始边界
 *        start = document.querySelector('.life-cycle-wrap').offsetTop
 *      结束边界
 *        end = document.querySelector('.life-cycle-wrap').offsetTop + height
 *    运动的物体：
 *      开发生命周期位置向上 移动 80px
 *        块变化高度 BlockChangeHeight = 80px
 *        块的开始参数 blockInit = -80px
 *        变化比值 changeRatio = (getScrollTop() + getInnerHeight() - start) / height
 *        块的变化 = blockInit - changeRatio * BlockChangeHeight
 *      图标向上移动 210px
 *      高斯模糊 高度增加 50px
 *
 *    调整的内容
 */
import {
  defineComponent,
  ref,
  onUnmounted,
  onMounted,
} from '@vue/composition-api';
import { lifeCycleData, icons } from './mock-data';
import { getScrollTop, getInnerHeight } from '@/utils/common';
import ToRightPng from '@/assets/imgs/home/to-right.png';
import ScreenTitle from '@/views/home/components/screen-title.vue';

const titleInfo = {
  cnName: '开发生命周期',
  cnColor: 'rgba(53,64,90,1)',
  enName: 'Developmentlifecycle',
  enColor: 'rgba(62,142,253,0.1)',
};

/**
 * 随屏动画*/
function animate() {
  const lifeCycleEl = ref(null);
  const blockEl = ref(null);
  const icon1El = ref(null);
  const icon2El = ref(null);
  const icon3El = ref(null);
  const blurEl = ref(null);

  const scroll = () => {
    const height = 1500;
    const start = lifeCycleEl.value.offsetTop;
    const end = start + height;
    // 当前滚动条
    const currentScroll = getScrollTop() + getInnerHeight();

    /**
     * 设置块*/
    const setBlock = (changeRatio) => {
      const changeHeight = 80;
      const blockInit = -80;
      blockEl.value.style.marginTop = `${
        blockInit - changeRatio * changeHeight
      }px`;
    };

    /**
     * 设置图标*/
    const setIcons = (changeRatio) => {
      const changeHeight = 310;
      const icon1Init = 143;
      const icon2Init = 58;
      const icon3Init = -50;
      icon1El.value.style.top = `${icon1Init - changeRatio * changeHeight}px`;
      icon2El.value.style.top = `${icon2Init - changeRatio * changeHeight}px`;
      icon3El.value.style.bottom = `${
        icon3Init + changeRatio * changeHeight
      }px`;
    };

    /**
     * 设置高斯模糊*/
    const setBlur = (changeRatio) => {
      const changeHeight = 50;
      const blurInit = 80;
      blurEl.value.style.height = `${blurInit + changeRatio * changeHeight}px`;
    };
    if (currentScroll >= start && currentScroll <= end) {
      /**
       * 触发修改*/
      const changeRatio = (currentScroll - start) / height;
      setBlock(changeRatio);
      setIcons(changeRatio);
      setBlur(changeRatio);
    }
  };

  onMounted(() => {
    scroll();
    window.addEventListener('scroll', scroll);
  });
  onUnmounted(() => {
    window.removeEventListener('scroll', scroll);
  });

  return {
    lifeCycleEl,
    blockEl,
    icon1El,
    icon2El,
    icon3El,
    blurEl,
  };
}
export default defineComponent({
  components: {
    ScreenTitle,
  },

  setup() {
    const {
      lifeCycleEl,
      blockEl,
      icon1El,
      icon2El,
      icon3El,
      blurEl,
    } = animate();

    return {
      titleInfo,
      lifeCycleData,
      ToRightPng,
      icons,
      lifeCycleEl,
      blockEl,
      icon1El,
      icon2El,
      icon3El,
      blurEl,
    };
  },
});
</script>
