<template>
  <div
    class="technology-panorama j-flex j-justify-center j-text-white j-relative"
  >
    <div class="screen-main">
      <screen-title :info="titleInfo" class="title j-mb-8" />
      <div>
        <div class="j-flex j-justify-center j-mb-12">
          <el-radio-group
            v-model="activeIdx"
            size="large"
            class="el-radio-group-technology"
          >
            <el-radio-button :label="0">
              <span>基础设施</span>
            </el-radio-button>
            <el-radio-button
              v-for="(it, idx) of architectureDiagrams"
              :key="idx"
              :label="idx + 1"
              >{{ it.title }}</el-radio-button
            >
          </el-radio-group>
        </div>

        <swiper
          ref="mySwiper"
          :options="swiperOptions"
          class="j-overflow-hidden"
        >
          <swiper-slide>
            <div>
              <ul class="swiper-base-station j-mb-6">
                <li
                  v-for="(it, idx) of baseStation"
                  :key="idx"
                  class="swiper-base-station-item-wrap"
                >
                  <div class="swiper-base-station-item">
                    <div class="j-flex j-flex-col j-items-center">
                      <img :src="it.img" class="j-mb-5" />
                      <div class="j-text-lg j-font-semibold title j-mb-4">
                        {{ it.title }}
                      </div>
                    </div>
                    <div class="j-flex j-flex-wrap">
                      <span
                        v-for="(itChild, idxChild) of it.children"
                        :key="idxChild"
                        :class="[itChild.type]"
                        class="ability j-truncate j-mb-2"
                      >
                        {{ itChild.title }} {{ idxChild.type }}
                      </span>
                    </div>
                  </div>
                </li>
              </ul>
              <div class="j-flex swiper-base-station-desc j-text-white">
                <span class="j-ml-auto">不同文字颜色的代表技术建设状态:</span>
                <div
                  v-for="(it, idx) of baseStationType"
                  :key="idx"
                  class="j-flex j-items-center j-ml-6"
                >
                  <span :class="it.value" class="icon-desc"></span>
                  <span class="j-ml-2">{{ it.name }}</span>
                </div>
              </div>
            </div>
          </swiper-slide>
          <swiper-slide
            v-for="(it, idx) of architectureDiagrams"
            :key="idx"
            class="swiper-slide-architecture"
          >
            <img :src="it.img" />
          </swiper-slide>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { reactive, ref, toRefs, watch } from '@vue/composition-api';
// import { RadioGroup, RadioButton } from '@jdt/find';
import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper';

import {
  baseStation,
  architectureDiagrams,
  baseStationType,
  icons,
} from './mock-data';
import ScreenTitle from '@/views/home/components/screen-title.vue';

// Vue.use(RadioGroup);
// Vue.use(RadioButton);

const titleInfo = {
  cnName: '技术全景图',
  cnColor: '#ffffff',
  enName: 'Technologypanorama',
  enColor: 'rgba(255,255,255,0.04)',
};
export default {
  name: 'TechnologyPanorama',

  directives: {
    swiper: directive,
  },

  components: {
    ScreenTitle,
    Swiper,
    SwiperSlide,
  },

  setup() {
    const state = reactive({
      swiperOptions: {
        simulateTouch: false, // 禁用滑动播放
      },
      activeIdx: 0,
    });

    let mySwiper = ref(null);

    /**
     * slideTo*/

    watch(
      () => state.activeIdx,
      (idx) => {
        mySwiper.value.$swiper.slideTo(idx, 1000, false);
      }
    );

    return {
      titleInfo,
      baseStation,
      architectureDiagrams,
      baseStationType,
      icons,
      mySwiper,
      ...toRefs(state),
    };
  },
};
</script>

