<template>
  <div
    ref="lifeCycleEl"
    class="top-products j-flex j-justify-center j-relative"
  >
    <div class="screen-main">
      <screen-title :info="titleInfo" class="title" />
      <div>
        <div class="j-flex j-justify-center j-mb-8">
          <el-tabs class="j-inline-flex" @tab-click="slideTo">
            <el-tab-pane
              v-for="(it, idx) of topProducts"
              :key="idx"
              :label="it.name"
            ></el-tab-pane>
          </el-tabs>
        </div>

        <swiper
          ref="mySwiper"
          :options="swiperOptions"
          class="j-overflow-hidden shadow"
        >
          <swiper-slide
            v-for="(it, idx) of topProducts"
            :key="idx"
            class="my-swiper-slide"
          >
            <div class="j-flex">
              <div class="j-flex-1 slider-left">
                <div class="j-mb-6 slider-left-title">{{ it.name }}</div>
                <div class="j-mb-12 desc">{{ it.desc }}</div>
                <el-button v-if="!Array.isArray(it.id)" type="primary" class="action-btn"  @click="gotoTop(it)"
                >查看更多</el-button>

                <div v-else>
                  <el-button v-for="itC of it.id" type="primary" class="action-btn" @click="gotoTop({
                    type: it.it,
                    id: itC.id
                  })"
                  >{{itC.text}}</el-button>

                </div>

              </div>
              <img :src="it.img" class="slider-right" />
            </div>
          </swiper-slide>
        </swiper>
      </div>
      <img ref="icon1El" :src="icons.ring1" class="icon-ring j-absolute" />
      <img
        ref="icon2El"
        :src="icons.product1"
        class="icons-product1 j-absolute"
      />
    </div>
  </div>
</template>
<script>
/**
 * 转场：
 *  头部用tabs
 *  内容转场用 swiper
 *  */

import Vue from 'vue';
import {
  defineComponent,
  reactive,
  toRefs,
  ref,
  onMounted,
  onUnmounted,
} from '@vue/composition-api';
// import { Tabs, TabPane, Button } from '@jdt/find';
import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper';

import { getScrollTop, getInnerHeight } from '@/utils/common';
import { topProducts, icons } from './mock-data';

import ScreenTitle from '@/views/home/components/screen-title.vue';

// Vue.use(Tabs);
// Vue.use(TabPane);
// Vue.use(Button);

const titleInfo = {
  cnName: '产品/文章',
  cnColor: '#35405A',
  enName: 'Products / articles',
  enColor: 'rgba(62,142,253,0.1)',
};

function animate() {
  const lifeCycleEl = ref(null);
  const icon1El = ref(null);
  const icon2El = ref(null);

  const scrollProduct = () => {
    const height = 1000;
    const start = lifeCycleEl.value.offsetTop;
    const end = start + height;
    // 当前滚动条
    const currentScroll = getScrollTop() + getInnerHeight();

    /**
     * 设置图标*/
    const setIcons = (changeRatio) => {
      const changeHeight = 280;
      const icon1Init = 0;
      const icon2Init = 250;
      icon1El.value.style.top = `${icon1Init - changeRatio * changeHeight}px`;
      icon2El.value.style.top = `${icon2Init - changeRatio * changeHeight}px`;
    };

    if (currentScroll >= start && currentScroll <= end) {
      /**
       * 触发修改*/
      const changeRatio = (currentScroll - start) / height;
      setIcons(changeRatio);
    }
  };

  onMounted(() => {
    scroll();
    window.addEventListener('scroll', scrollProduct);
  });
  onUnmounted(() => {
    window.removeEventListener('scroll', scrollProduct);
  });

  return {
    lifeCycleEl,
    icon1El,
    icon2El,
  };
}
export default defineComponent({
  directives: {
    swiper: directive,
  },

  components: {
    ScreenTitle,
    Swiper,
    SwiperSlide,
  },

  setup(_, { root: { $router } }) {
    const state = reactive({
      swiperOptions: {
        simulateTouch: false, // 禁用滑动播放
      },
    });

    const { lifeCycleEl, icon1El, icon2El } = animate();

    let mySwiper = ref(null);

    /**
     * slideTo*/
    const slideTo = (tab) => {
      mySwiper.value.$swiper.slideTo(tab.index, 1000, false);
    };

    /**
     * 查看详情*/
    const gotoTop = ({ type, id }) => {
      type === 'product'
        ? $router.push({
            name: 'product-detail',
            params: { id },
          })
        : window.open(id);
    };

    return {
      titleInfo,
      topProducts,
      icons,
      mySwiper,
      ...toRefs(state),
      lifeCycleEl,
      icon1El,
      icon2El,
      slideTo,
      gotoTop,
    };
  },
});
</script>
